import React from "react";
import { withRouter, Link } from "react-router-dom";

import { Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import profile from "../../assets/profile.jpg";
import { pages, links } from "./menu-items";

import "./menu.styles.scss";

class Menu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      active: null
    };
  }

  componentDidMount() {
    const { pathname } = this.props.location;
    const initialPage = pages.find(page => page.to === pathname);
    this.setState({ active: initialPage.name });
  }

  handleClick = e => {
    const currentPage = e.target.text;
    this.setState({ active: currentPage });
  };

  render() {
    const { active } = this.state;
    const aProps = { target: "_blank", rel: "noopener noreferrer" };

    return (
      <Col className="menu">
        <img width="120" height="120" src={profile} alt="profile" />

        <ul className="links">
          {pages.map((page, idx) => (
            <li key={idx}>
              {page.to ? (
                <Link
                  to={page.to}
                  onClick={this.handleClick}
                  className={page.name === active ? "active" : null}
                >
                  {page.name}
                </Link>
              ) : (
                page.name
              )}
              {page.href ? (
                <a href={page.href} className="link-to-code" {...aProps}>
                  {" "}
                  (See code <FontAwesomeIcon icon={faExternalLinkAlt} /> )
                </a>
              ) : null}
            </li>
          ))}
        </ul>

        <div className="icon-container">
          {links.map((link, idx) => (
            <a key={idx} href={link.href} {...aProps}>
              <FontAwesomeIcon icon={link.icon} />
            </a>
          ))}
        </div>
      </Col>
    );
  }
}

export default withRouter(Menu);
